import * as React from "react";
import styled from "@emotion/styled";
import { colors } from "../../../design-system";

interface Item {
  id: string;
  color: string;
  category: string;
  label: string;
  value: string;
}

interface Props {
  items: Item[];
}

const Bar = styled.div(({ color }: any) => ({
  transition: "color 200ms ease",
  background: color || colors.blue,
}));

const BarLine: React.FunctionComponent<Props> = ({ items }) => {
  return (
    <div>
      {items.map((item: Item) => (
        <Bar {...item} />
      ))}
    </div>
  );
};

export default BarLine;
